.button{
display: inline-block;
padding: 2vw 2vw;
font-size: 3vw;
cursor: pointer;
text-align: center;
text-decoration: none;
outline: 1px;
color: #fff;
background-color: #4CAF50;
border: none;
border-radius: 15px;
box-shadow: 0.3vw 0.4vw #999;
}

.button:active {
  background-color: #3e8e41;
  box-shadow: 0.3vw 0.4vw #666;
  -webkit-transform: translateY(4px);
          transform: translateY(4px);
}

.button:hover {background-color: #3e8e41}
.dialogStyles {
    width: '500px';
    maxWidth: '100%';
    margin: '0 auto';
    position: 'fixed';
    left: '50%';
    top: '50%';
    -webkit-transform: 'translate(-50%,-50%)';
            transform: 'translate(-50%,-50%)';
    zIndex: '999';
    backgroundColor: '#eee';
    padding: '10px 20px 40px';
    border-radius: '8px';
    display: 'flex';
    flexDirection: 'column';
};
.dialogCloseButtonStyles{
    marginBottom: '15px';
    padding: '3px 8px';
    cursor: 'pointer';
    borderRadius: '50%';
    border: 'none';
    width: '30px';
    height: '30px';
    fontWeight: 'bold';
    alignSelf: 'flex-end';
};
