.HighInfo{
    display: grid;
    grid-template-areas:
         'Vis WSpeed'
         'Precip Rain';
    padding: 2%;
    grid-template-columns: 50% 50%;
  }

.Img{
     height: 50vw;
     width: 70vw;
     -webkit-filter: drop-shadow(3px 3px 2px rgba(0, 0, 0, 0.5));
     filter:         drop-shadow(3px 3px 2px rgba(0, 0, 0, 0.5)); 
  }

.SlideTime {grid-area: stime;} 
.SlideTemp {grid-area: stemp;} 
.SlideConditions {grid-area: scond;} 
.Slide {grid-area: slide;} 

.Slider{
    display: grid;
    grid-template-areas:
      'stime stemp scond'
      'slide slide slide';
    padding: 2%;
    grid-template-columns: 1fr 1fr 1fr;
  }

  .slidecontainer {
    width: 100%;
  }
  
  .Slide {
    -webkit-appearance: none;
    width: 100%;
    height: 25px;
    background: #d3d3d3;
    outline: none;
    opacity: 0.7;
    -webkit-transition: .2s;
    -webkit-transition: opacity .2s;
    transition: opacity .2s;
    -webkit-filter: drop-shadow(5px 5px 1px rgba(0, 0, 0, 0.3));
    filter:         drop-shadow(5px 5px 1px rgba(0, 0, 0, 0.3)); 
  }
  
  .Slide:hover {
    opacity: 1;
  }
  
  .Slide::-webkit-slider-thumb {
    -webkit-appearance: none;
    appearance: none;
    width: 5%;
    height: 25px;
    background: rgb(10, 206, 255);
    cursor: pointer;
  }
  
  .Slide::-moz-range-thumb {
    width: 25px;
    height: 25px;
    background: #4CAF50;
    cursor: pointer;
  }

